var _ = Package.underscore._,
    package_name = "telescope:settings",
    namespace = "telescope:settings";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["th"])) {
  TAPi18n.translations["th"] = {};
}

if(_.isUndefined(TAPi18n.translations["th"][namespace])) {
  TAPi18n.translations["th"][namespace] = {};
}

_.extend(TAPi18n.translations["th"][namespace], {"title":"ชื่อเรื่อง"});
TAPi18n._registerServerTranslator("th", namespace);
